/**
 *
 * Component to show the properties of a ticket
 * Author : Daniel Contreras Aladro
 * Date : 2017-02-21
 *
 */
import { Component, OnInit, Input, OnDestroy } from '@angular/core';

import { TranslateService } from '@ngx-translate/core';

import * as $ from 'jquery/dist/jquery';

import { ZendeskService } from '../../services/zendesk.service';
import { LoginService } from '../../services/login.service';

import { Ticket } from '../../classes/ticket';
import { Attachment } from '../../classes/attachment';

@Component({
  selector: 'app-ticket-data',
  templateUrl: './ticket-data.component.html',
  styleUrls: ['./ticket-data.component.css']
})
export class TicketDataComponent implements OnInit, OnDestroy {
  /**
   * Properties and objects of the component
   */
  @Input() ticket: Ticket;

  files: Attachment[];

  errorMessage: string;
  /**
   * constructor - Constructor of the component
   */
  constructor(
    private translate: TranslateService,
    private zendeskService: ZendeskService,
    public loginService: LoginService
  ) { }
  /**
   * ngOnDestroy - OnDestroy method of the component
   */
  ngOnDestroy() {
    $('.ng-tool-tip-content').hide();
  }
  /**
   * ngOnInit - OnInit method of the component
   */
  ngOnInit() {
    this.files = [];

    this.zendeskService.getTicketComments(this.ticket.id)
    .then(
      responseData => {
        if (responseData !== undefined && responseData !== null) {
          responseData.forEach((comment) => {
            if(comment.attachments){
              comment.attachments.forEach((file) => {
                this.files.push(file);
              });
            }
          });
        }
      },
      error => {
        this.translate.get('Error! Service Unavailable').subscribe((res: string) => {
            this.errorMessage = res;
        });
      }
    );

    this.zendeskService.getUserIdentity(this.ticket.requester_id)
    .then(
        responseData => {
          if (responseData !== undefined && responseData !== null) {
            this.ticket.submitter_name = responseData.name;
          }
        },
        error => {
          this.translate.get('Error! Service Unavailable').subscribe((res: string) => {
              this.errorMessage = res;
          });
        }
    );

    this.ticket.custom_fields.forEach(
      custom_field => {
        this.zendeskService.getCustomField(custom_field.id)
        .then(
          responseData => {
            if (responseData !== undefined && responseData !== null) {
              custom_field.title_in_portal = responseData.title_in_portal;
              custom_field.custom_field_options = responseData.custom_field_options;
              custom_field.system_field_options = responseData.system_field_options;
            }
          },
          error => {
            this.translate.get('Error! Service Unavailable').subscribe((res: string) => {
                this.errorMessage = res;
            });
          }
        );
      }
    );
  }
  /**
   * getValue - Method to return a readable value of a field
   *
   * @param {any}  fieldData Object with the custom field data
   *
   * @return {string}  Returns a string to show in the screen to the user
   */
  getValue(fieldData: any){
    if(fieldData.custom_field_options){
      let valueData = fieldData.custom_field_options.filter(field => field.value === fieldData.value);
      return valueData[0]!==undefined?valueData[0].name:"Not specified";
    }else if(fieldData.system_field_options){
      let valueData = fieldData.system_field_options.filter(field => field.value === fieldData.value);
      return valueData[0]!==undefined?valueData[0].name:"Not specified";
    }else{
      let valueReturn = fieldData.value!==null?fieldData.value:"Not specified"
      if(typeof(valueReturn) === "boolean"){
        if(valueReturn){
          valueReturn = 'YES';
        }else{
          valueReturn = 'NO';
        }
      }
      return valueReturn;
    }
  }

  addFiles(files:Attachment[]){
    if(files){
      files.forEach((file) => {
        this.files.push(file);
      });
    }
  }
}



