import { Field } from './field';
import { Comment } from './comment';

export class Ticket {
  id: number;
  subject: string;
  description: string;
  type: string;
  priority: string;
  created: string;
  updated: string;
  status: string;
  requester_id: string;
  submitter_name: string;
  custom_fields: Field[];
  comment: Comment;
}



