/**
 *
 * Component to show the login form
 * Author : Daniel Contreras Aladro
 * Date : 2017-02-21
 *
 */
import { Component, OnInit, OnDestroy } from '@angular/core';
import { Router,ActivatedRoute } from '@angular/router';

import { TranslateService } from '@ngx-translate/core';

import * as $ from 'jquery/dist/jquery';

import * as CryptoJS from 'crypto-js/crypto-js';

import { ZendeskService } from '../../services/zendesk.service';
import { LoginService } from '../../services/login.service';
import { PayaraService } from '../../services/payara.service';

import { User } from '../../classes/user';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnDestroy {
  /**
   * Properties and objects of the component
   */
  public user = new User();
  errorMessage: string;
  /**
   * constructor - Constructor of the component
   */
  constructor(
    private translate: TranslateService,
    private route: ActivatedRoute,
    private router: Router,
    public zendeskService: ZendeskService,
    public loginService: LoginService,
    public payaraService: PayaraService
  ) {}
  /**
   * ngOnDestroy - OnDestroy method of the component
   */
  ngOnDestroy() {
    $('.ng-tool-tip-content').hide();
    if(this.user.id !== undefined){
        this.loginService.user = this.user;
    }
  }
  /**
   * loginToZendesk - Submit method to login a user to the API and redirect to the ticket list
   *
   * @param {User}  user Object with the data of the user to login
   */
  loginToZendesk(user: User) {
    var regExpEmail = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    if(user!==undefined && user!==null && user.email!==undefined && user.email!=='' && regExpEmail.test(user.email) && user.password!==undefined && user.password!==''){
      this.loginService.getOauthToken(user.email,user.password)
      .then(
        responseData => {
          if (responseData !== undefined && responseData !== null && responseData !== '') {
            let encryptedData = CryptoJS.AES.encrypt(user.email+'|'+responseData, 'payara').toString();
            this.loginService.connectionData.OauthToken = responseData;
            this.loginService.getUserData()
            .then(
              responseData => {
                if (responseData !== undefined && responseData !== null && responseData.id !== null) {
                  this.user = new User();
                  this.user = responseData;
                  this.user.token = this.loginService.connectionData.OauthToken;
                  this.loginService.user = this.user
                  localStorage.setItem('currentUser',JSON.stringify(this.user));
                  this.zendeskService.OAuthToken = this.loginService.connectionData.OauthToken;
                  this.payaraService.setStoredEmail(encryptedData)
                  .then(
                    responseData => {
                      if (responseData !== undefined && responseData !== null && responseData.exit_code === "SUCCESS") {
                        localStorage.setItem('showMessage',null);
                        this.router.navigate(['/list']);
                      } else {
                        this.user = new User();
                        this.loginService.user = this.user;
                        this.zendeskService.OAuthToken = '';
                        this.translate.get('Error! User stored but bad response').subscribe((res: string) => {
                            this.errorMessage = res;
                        });
                      }
                    },
                    error => {
                      this.user = new User();
                      this.loginService.user = this.user;
                      this.zendeskService.OAuthToken = '';
                      this.translate.get('Error! User not stored').subscribe((res: string) => {
                          this.errorMessage = res;
                      });
                    }
                  );
                } else {
                  this.user = new User();
                  this.loginService.user = this.user;
                  this.zendeskService.OAuthToken = '';
                  this.translate.get('Error! User not found').subscribe((res: string) => {
                      this.errorMessage = res;
                  });
                }
              },
              error => {
                this.user = new User();
                this.loginService.user = this.user;
                this.zendeskService.OAuthToken = null;
                this.translate.get('Error! Service Unavailable').subscribe((res: string) => {
                    this.errorMessage = res;
                });
              }
            );
          }
        },
        error => {
          this.user = new User();
          this.loginService.user = this.user;
          this.zendeskService.OAuthToken = null;
          this.translate.get('Error! User or password invalid').subscribe((res: string) => {
              this.errorMessage = res;
          });
        }
      );
    } else {
      if(!regExpEmail.test(user.email)){
        this.translate.get('invalid-email', {value: user.email}).subscribe((res: string) => {
            this.errorMessage = res;
        });
      } else{
        this.translate.get('Error! User or password invalid').subscribe((res: string) => {
            this.errorMessage = res;
        });
      }
    }
  }
  /**
   * cleanError - Method to clean the error message when a key is pressed on the input box
   *
   * @param {any}  event Object with the event data
   */
  cleanError(event:any){
    this.errorMessage = "";
  }
  /**
   * shopSupport - Method to send the user to the support buy website
   */
  shopSupport(){
    window.open(this.loginService.connectionData.shopURL)
  }

}



