/**
 *
 * Component to attach files to the storage
 * Author : Daniel Contreras Aladro
 * Date : 2017-02-21
 *
 */
declare var JSZipUtils: any;

import {
  Component,
  OnInit, OnDestroy,
  AfterViewInit,
  Input,
  Output,
  EventEmitter } from '@angular/core';

import { Observable, Subscription } from 'rxjs/Rx';


import { environment } from '../../../environments/environment';

import tinymce from 'tinymce/tinymce';
import 'tinymce/themes/modern/theme';

// Plugins
import 'tinymce/plugins/paste/plugin';
import 'tinymce/plugins/searchreplace/plugin';

import { TranslateService } from '@ngx-translate/core';

import * as $ from 'jquery/dist/jquery';

import { ZendeskService } from '../../services/zendesk.service';
import { PayaraService } from '../../services/payara.service';

import { Attachment } from '../../classes/attachment';

@Component({
  selector: 'app-add-file',
  templateUrl: './add-file.component.html',
  styleUrls: ['./add-file.component.css'],
})
export class AddFileComponent implements OnInit,AfterViewInit, OnDestroy {
  /**
   * Properties and objects of the component
   */
  environment = environment;
  tinymce = tinymce;

  groups:any = [];
  staticFileButtons:any = [
    {
      title:'Monitoring Data',
      url:'get-monitoring-configuration.json',
      group:'general',
      uploaded:false,
      type:'json'
    },
    {
      title:'Domain Log',
      url:'view-log',
      group:'general',
      uploaded:false,
      type:'log'
    },
    {
      title:'Health Checks',
      url:'configs/config/server-config/health-check-service-configuration/list-historic-healthchecks.json',
      group:'general',
      uploaded:false,
      type:'health'
    },
    {
      title:'Request Traces',
      url:'configs/config/server-config/request-tracing-service-configuration/list-historic-requesttraces',
      group:'general',
      uploaded:false,
      type:'traces'
    },
  ];

  fileButtons:any = [];

  filesLoaded:boolean = false;
  filesSaved:boolean = false;

  @Input() title: string;

  @Output() saved = new EventEmitter<Attachment>();
  @Output() removed = new EventEmitter<string>();

  private sub: Subscription;

  errorMessage: string;
  successMessage: string;
  loadingMessage: string;
  loadingMessageXml: string;

  elementId:string = 'domainEditor';
  isVisibleEditor:boolean = true;
  xmlLoaded:boolean = false;
  xmlLoading:string = 'LOADING';
  xmlColor:string = 'warning';

  editor:any;

  /**
   * ngAfterViewInit - AfterViewInit method of the component
   */
  ngAfterViewInit() {
    this.discardXml();
  }
  /**
   * constructor - Constructor of the component
   */
  constructor(
    private translate: TranslateService,
    private zendeskService: ZendeskService,
    private payaraService: PayaraService
  ) {
  }
  /**
   * ngOnDestroy - OnDestroy method of the component
   */
  ngOnDestroy() {
    $('.ng-tool-tip-content').hide();
    if(this.sub) this.sub.unsubscribe();
  }
  filter(buttons:any[],group:string){
    return buttons.filter(data => data.group === group);
  }
  /**
   * ngOnInit - OnInit method of the component
   */
  ngOnInit() {
    this.groups.push('general');
    this.fileButtons = Object.assign([], this.staticFileButtons);
    this.payaraService.getServerInstances()
    .then(
      responseData => {
        if (responseData !== undefined && responseData !== null) {
          this.filesLoaded = true;
          let responseArray = Object.keys(responseData);
          for (let prop in responseArray) {
            if(responseArray[prop] === 'server'){
              this.fileButtons.push({
                title: this.translate.instant('JVM Report')+ ': ' +responseArray[prop],
                url:'servers/server/'+responseArray[prop]+'/generate-jvm-report.json?type=summary',
                group:'server',
                uploaded:false,
                type:'json'
              });
              this.fileButtons.push({
                title: this.translate.instant('Thread Dump')+ ': ' +responseArray[prop],
                url:'servers/server/'+responseArray[prop]+'/generate-jvm-report.json?type=thread',
                group:'server',
                uploaded:false,
                type:'json'
              });
              this.fileButtons.push({
                title: 'Log: ' +responseArray[prop],
                url:'logs_'+responseArray[prop]+'.zip?contentSourceId=LogFiles&target='+responseArray[prop]+'&restUrl='+window.location.protocol + '//' + window.location.hostname + ':' + window["globalPort"]+'/management/domain',
                group:'server',
                file:'logs_'+responseArray[prop]+'.zip',
                uploaded:false,
                type:'zip'
              });
            }else{
              this.payaraService.getInstanceStatus(responseArray[prop])
              .then(
                responseData => {
                  if (responseArray[prop] === 'server' || (responseData !== undefined && responseData !== null && responseData === 'RUNNING')) {
                    this.groups.push(responseArray[prop]);
                    this.fileButtons.push({
                      title: this.translate.instant('JVM Report')+ ': ' +responseArray[prop],
                      url:'servers/server/'+responseArray[prop]+'/generate-jvm-report.json?type=summary',
                      group:responseArray[prop],
                      uploaded:false,
                      type:'json'
                    });
                    this.fileButtons.push({
                      title: this.translate.instant('Thread Dump')+ ': ' +responseArray[prop],
                      url:'servers/server/'+responseArray[prop]+'/generate-jvm-report.json?type=thread',
                      group:responseArray[prop],
                      uploaded:false,
                      type:'json'
                    });
                    this.fileButtons.push({
                      title: 'Log: ' +responseArray[prop],
                      url:'Log: ' +responseArray[prop]+'.zip?contentSourceId=LogFiles&target='+responseArray[prop]+'&restUrl='+window.location.protocol + '//' + window.location.hostname + ':' + window["globalPort"]+'/management/domain',
                      group:responseArray[prop],
                      file:'Log: ' +responseArray[prop]+'.zip',
                      uploaded:false,
                      type:'zip'
                    });
                  }
                },
                error => {
                  this.translate.get('Error! Service Unavailable').subscribe((res: string) => {
                      this.errorMessage = res;
                  });
                }
              );
            }
          }
        }
      },
      error => {
        this.translate.get('Error! Service Unavailable').subscribe((res: string) => {
            this.errorMessage = res;
        });
      }
    );
  }
  /**
  * searchFile - Event to get the data for the file when the button is pressed
  *
  * @param {any}  fileButton Object with the data of the fileButton
  */
  searchFile(fileButton:any) {
    this.errorMessage = "";
    this.loadingMessage = "";
    this.successMessage = "";
    if(fileButton.uploaded){
      this.removed.emit(fileButton.title);
      fileButton.uploaded = false;
    }else{
      this.loadingMessage = "Loading file ..." + fileButton.title;
      if(fileButton.url && fileButton.url!==''){
        switch(fileButton.type) {
            case 'log':
              this.getFile(fileButton.url,false,(response) => {
                if(response!==false){
                  this.addFile(
                    response['_body'],
                    fileButton.title+'.log',
                    'application/octet-stream',
                    (response) => {
                      if(response){
                        this.saved.emit(response);
                        fileButton.uploaded = true;
                        this.filesSaved = true;
                        this.loadingMessage = null;
                        this.successMessage = "";
                        this.translate.get('File added successfully!').subscribe((res: string) => {
                            this.successMessage = res;
                        });
                        let timer = Observable.timer(1000,50);
                        this.sub = timer.subscribe(t=>
                          {
                            this.successMessage = "";
                            this.sub.unsubscribe();
                          }
                        );
                      }else{
                        fileButton.uploaded = false;
                        this.loadingMessage = null;
                        this.successMessage = "";
                      }
                    }
                  );
                }else{
                  this.translate.get('Empty data1').subscribe((res: string) => {
                      this.errorMessage = res;
                      let timer = Observable.timer(1000,50);
                      this.sub = timer.subscribe(t=>
                        {
                          this.errorMessage = "";
                          this.sub.unsubscribe();
                        }
                      );
                      fileButton.uploaded = false;
                      this.loadingMessage = null;
                      this.successMessage = "";
                  });
                }
              });
              break;
          case 'traces':
            this.payaraService.postFile(fileButton.url).then(
              responseData =>{
                if(responseData!==false && responseData.message!=="" && responseData.exit_code!=='FAIULRE' && responseData.message.indexOf('is not enabled')<0){
                  this.addFile(
                    responseData.message,
                    fileButton.title+'.json',
                    'application/octet-stream',
                    (response) => {
                      if(response){
                        this.saved.emit(response);
                        fileButton.uploaded = true;
                        this.filesSaved = true;
                        this.loadingMessage = null;
                        this.successMessage = "";
                        this.translate.get('File added successfully!').subscribe((res: string) => {
                            this.successMessage = res;
                        });
                        let timer = Observable.timer(1000,50);
                        this.sub = timer.subscribe(t=>
                          {
                            this.successMessage = "";
                            this.sub.unsubscribe();
                          }
                        );
                      }else{
                        fileButton.uploaded = false;
                        this.loadingMessage = null;
                        this.successMessage = "";
                      }
                    }
                  );
                }else{
                  this.translate.get('Empty data1').subscribe((res: string) => {
                      this.errorMessage = res;
                      let timer = Observable.timer(1000,50);
                      this.sub = timer.subscribe(t=>
                        {
                          this.errorMessage = "";
                          this.sub.unsubscribe();
                        }
                      );
                      fileButton.uploaded = false;
                      this.loadingMessage = null;
                      this.successMessage = "";
                  });
                }
              },
              error => {
                this.translate.get('Error! Service Unavailable').subscribe((res: string) => {
                    this.errorMessage = res;
                    let timer = Observable.timer(1000,50);
                    this.sub = timer.subscribe(t=>
                      {
                        this.errorMessage = "";
                        this.sub.unsubscribe();
                      }
                    );
                    fileButton.uploaded = false;
                    this.loadingMessage = null;
                    this.successMessage = "";
                });
              }
            );
            break;
          case 'health':
            this.getFile(fileButton.url,false,(response) => {
              if(response!==false && response.extraProperties!==undefined && response.extraProperties.historicmessages.length>0){
                this.addFile(
                  JSON.stringify(response.json().extraProperties.historicmessages),
                  fileButton.title+'.json',
                  'application/octet-stream',
                  (response) => {
                    if(response){
                      this.saved.emit(response);
                      fileButton.uploaded = true;
                      this.filesSaved = true;
                      this.loadingMessage = null;
                      this.successMessage = "";
                      this.translate.get('File added successfully!').subscribe((res: string) => {
                          this.successMessage = res;
                      });
                      let timer = Observable.timer(1000,50);
                      this.sub = timer.subscribe(t=>
                        {
                          this.successMessage = "";
                          this.sub.unsubscribe();
                        }
                      );
                    }else{
                      fileButton.uploaded = false;
                      this.loadingMessage = null;
                      this.successMessage = "";
                    }
                  }
                );
              }else{
                fileButton.uploaded = false;
                this.loadingMessage = null;
                this.successMessage = "";
              }
            });
            break;
          case 'json':
            this.getFile(fileButton.url,false,(response) => {
              if(response!==false && response.json().message!==undefined && response.json().message!==""){
                this.addFile(
                  response.json().message,
                  fileButton.title+'.json',
                  'application/octet-stream',
                  (response) => {
                    if(response){
                      this.saved.emit(response);
                      fileButton.uploaded = true;
                      this.filesSaved = true;
                      this.loadingMessage = null;
                      this.successMessage = "";
                      this.translate.get('File added successfully!').subscribe((res: string) => {
                          this.successMessage = res;
                      });
                      let timer = Observable.timer(1000,50);
                      this.sub = timer.subscribe(t=>
                        {
                          this.successMessage = "";
                          this.sub.unsubscribe();
                        }
                      );
                    }else{
                      fileButton.uploaded = false;
                      this.loadingMessage = null;
                      this.successMessage = "";
                    }
                  }
                );
              }
            });
            break;
          case 'zip':
            this.getFile(fileButton.url,true,(response2) => {
              if(response2!==false){
                this.addFile(
                  new File([response2], fileButton.file, {type:'application/zip'}),
                  fileButton.file,
                  'application/zip',
                  (result) => {
                    if(result){
                      this.saved.emit(result);
                      fileButton.uploaded = true;
                      this.filesSaved = true;
                      this.loadingMessage = null;
                      this.successMessage = "";
                      this.translate.get('File added successfully!').subscribe((res: string) => {
                          this.successMessage = res;
                      });
                      let timer = Observable.timer(1000,50);
                      this.sub = timer.subscribe(t=>
                        {
                          this.successMessage = "";
                          this.sub.unsubscribe();
                        }
                      );
                    }else{
                      fileButton.uploaded = false;
                      this.loadingMessage = null;
                      this.successMessage = "";
                    }
                  }
                );
              }
            });
            break;
        }
      }
    }
  }
  /**
  * cleanFiles - Method to clean the add file buttons and enable it
  */
  cleanFiles(){
    this.filesSaved = false;
    this.filesLoaded = false;
    this.xmlLoaded = false;
    this.isVisibleEditor = false;
    this.xmlColor = 'default';
    this.xmlLoading = 'NO';
    for (let button of this.fileButtons) {
        button.uploaded = false;
    }
  }
  /**
  * searchFile - Event to get the data for the file when the button is pressed
  *
  * @param {any}      content Object with file's data
  * @param {string}   name String with file's name
  * @param {string}   type String with file's mimetype
  * @param {any}      back Callback function to return sync value
  */
  addFile(content:any,name:string,type:string,back:any){
    if(!(content instanceof String)){
      this.zendeskService.addNewFile(content, name,type)
      .then(
        responseData => {
          if (responseData !== undefined && responseData !== null) {
            back(JSON.parse(responseData).upload);
          }
        },
        error => {
          this.translate.get('Error! Service Unavailable').subscribe((res: string) => {
              this.errorMessage = res;
              let timer = Observable.timer(1000,50);
              this.sub = timer.subscribe(t=>
                {
                  this.errorMessage = "";
                  this.sub.unsubscribe();
                }
              );
              back(false);
          });
        }
      );
    }else{
      if(content.indexOf('offline') > 0){
        this.zendeskService.addNewFile(content, name,type)
        .then(
          responseData => {
            if (responseData !== undefined && responseData !== null) {
              back(JSON.parse(responseData).upload);
            }
          },
          error => {
            this.translate.get('Error! Service Unavailable').subscribe((res: string) => {
                this.errorMessage = res;
                let timer = Observable.timer(1000,50);
                this.sub = timer.subscribe(t=>
                  {
                    this.errorMessage = "";
                    this.sub.unsubscribe();
                  }
                );
                back(false);
            });
          }
        );
      } else {
        this.translate.get('Instance seems to be offline').subscribe((res: string) => {
            this.errorMessage = res;
            let timer = Observable.timer(1000,50);
            this.sub = timer.subscribe(t=>
              {
                this.errorMessage = "";
                this.sub.unsubscribe();
              }
            );
            back(false);
        });
      }
    }
  }
  /**
  * searchFile - Event to get the data for the file when the button is pressed
  *
  * @param {string}   url String with the endpoint to call
  * @param {boolean}  parse Boolean to know if parse data is needed
  * @param {any}      back Callback function to return sync value
  */
  getFile(url:string,parse:boolean,back:any){
    if(parse){
      JSZipUtils.getBinaryContent(
        this.payaraService.connectionData.filesURL + url,
        (err,data) => {
          if(err) {
            this.translate.get('Error! Service Unavailable').subscribe((res: string) => {
                this.errorMessage = res;
                let timer = Observable.timer(1000,50);
                this.sub = timer.subscribe(t=>
                  {
                    this.errorMessage = "";
                    this.sub.unsubscribe();
                  }
                );
                back(false);
            });
          }else{
            back(data);
          }
        }
      );
    }else{
      this.payaraService.getFile(url)
      .then(
        responseData => {
          if (responseData !== undefined && responseData !== null) {
            back(responseData);
          }else{
            this.translate.get('Empty data2').subscribe((res: string) => {
                this.errorMessage = res;
                let timer = Observable.timer(1000,50);
                this.sub = timer.subscribe(t=>
                  {
                    this.errorMessage = "";
                    this.sub.unsubscribe();
                  }
                );
                back(false);
            });
          }
        },
        error => {
          this.translate.get('Error! Service Unavailable').subscribe((res: string) => {
              this.errorMessage = res;
              let timer = Observable.timer(1000,50);
              this.sub = timer.subscribe(t=>
                {
                  this.errorMessage = "";
                  this.sub.unsubscribe();
                }
              );
              back(false);
          });
        }
      );
    }
  }
  /**
   * showEditor - Function to show/hide xml editor
   */
  showEditor(){
    if(this.xmlLoading==='LOADING'){
      this.isVisibleEditor = false;
      this.xmlColor = 'default';
      this.xmlLoading = 'NO';
    }else if(this.xmlLoading==='YES'){
      this.removed.emit('Domain.xml');
      this.isVisibleEditor = false;
      this.xmlColor = 'default';
      this.xmlLoading = 'NO';
    }else if(this.xmlLoading==='NO'){
      this.isVisibleEditor = true;
      this.xmlColor = 'warning';
      this.xmlLoading = 'LOADING';
    }
  }
  /**
   * discardXml - Function to discard changes made to xml file shown
   */
  discardXml(){
    this.loadFileData();
    this.xmlLoaded = false;
    this.showEditor();
  }
  /**
   * loadFileData - Function to discard changes made to xml file shown
   */
  loadFileData(){
    this.getFile('configs/config/server-config/zendesk-support-configuration/get-domain-xml.json',false,
      (response) => {
        if (response !== undefined && response !== null) {
          this.tinymce.remove('#' + this.elementId);
          this.editor = null;
          let initObject = {
            relative_urls : false,
            remove_script_host : false,
            document_base_url : null,
            skin_url: '../assets/skins/lightgray',
            selector: '#' + this.elementId,
            plugins: ['paste','searchreplace'],
            elementpath: false,
            height : 300,
            menubar: false,
            toolbar:false,
            statusbar: false,
            forced_root_block : false,
            setup: editor => {this.editor = editor;},
          };
          if(this.environment.production){
            initObject.relative_urls = false;
            initObject.remove_script_host = false;
            initObject.document_base_url =  window.location.protocol + '//' + window.location.hostname + ':' + window["globalPort"]+'/resource/payara_support/zendesk/';
            initObject.skin_url = './assets/skins/lightgray';
          }
          this.tinymce.init(initObject);
          this.tinymce.activeEditor.setContent(response.json().message.replace(/>/g,'&gt;').replace(/</g,'<br>&lt;').replace('<br>',''));
        }
        /*else{
          this.translate.get('Empty data').subscribe((res: string) => {
              this.errorMessage = res;
              this.isVisibleEditor = false;
              this.xmlColor = 'default';
              this.xmlLoading = 'NO';
          });
        }*/
      }
    );
  }
  /**
   * saveXml - Function to save changes made to xml file shown
   */
  saveXml(){
    this.loadingMessageXml = "Loading Domain.xml file ...";
    this.addFile(
      this.editor.getContent().replace(/&gt;/g,'>').replace(/&lt;/g,'<').replace(/<br \/>/g,'\n'),
      'Domain.xml',
      'application/binary',
      (response) => {
        this.saved.emit(response);
        this.filesSaved = true;
        this.isVisibleEditor = false;
        this.xmlColor = 'success';
        this.xmlLoading = 'YES';
        this.tinymce.activeEditor.setMode('readonly');
        this.loadingMessageXml = null;
        this.successMessage = "";
        this.translate.get('File added successfully!').subscribe((res: string) => {
            this.successMessage = res;
        });
        let timer = Observable.timer(1000,50);
        this.sub = timer.subscribe(t=>
          {
            this.successMessage = "";
            this.sub.unsubscribe();
          }
        );
      }
    );
  }
  otherFile(event:any){
    let fileList: FileList = event.target.files;
    if(fileList.length > 0 && fileList[0].size < 20971520) {
        let file: File = fileList[0];
        let formData: FormData = new FormData();
        formData.append('upload', file, file.name);
        this.addFile(
          formData,
          file.name,
          'application/binary',
          (response) => {
            this.saved.emit(response);
            this.successMessage = "";
            this.translate.get('File added successfully!').subscribe((res: string) => {
                this.successMessage = res;
            });
            let timer = Observable.timer(1000,50);
            this.sub = timer.subscribe(t=>
              {
                this.successMessage = "";
                this.sub.unsubscribe();
              }
            );
          }
        );
      }else{
        this.translate.get('Error! File not exist or size bigger than 20 MB').subscribe((res: string) => {
          this.errorMessage = res;
          let timer = Observable.timer(1500,50);
          this.sub = timer.subscribe(t=>
            {
              this.errorMessage = "";
              this.sub.unsubscribe();
            }
          );
        });
      }
  }

}



