/**
 *
 * Pipe to show dates in a specific format
 * Author : Daniel Contreras Aladro
 * Date : 2017-02-21
 *
 */
import { Pipe, PipeTransform } from '@angular/core';
import * as moment from 'moment'

@Pipe({
  name: 'dayTime'
})
export class DayTimePipe implements PipeTransform {

  transform(value:any, args:string[]):any {
      if (value) {
        let d = moment(new Date(value));
        let now = moment(new Date());
        let std = d.format('DD/MM/YYYY HH:mm');
        let oneMonthAgo = moment(new Date()).subtract(1,'month');
        if(d.isBefore(oneMonthAgo)){
          std = d.fromNow();
        } else {
          let oneWeekAgo = moment(new Date()).subtract(1,'week');
          if(d.isBefore(oneWeekAgo)){
              std = d.format('D MMMM');
          } else {
            let oneDayAgo = moment(new Date()).subtract(1,'day');
            if(d.isBefore(oneDayAgo)){
              std = d.format('dddd HH:mm');
            } else {
              if(d.isSame(now,'day')){
                let oneHourAgo = moment(new Date()).subtract(1,'hour');
                if(d.isBefore(oneHourAgo)){
                  std = d.format('HH:mm');
                } else {
                  let minuteAgo = moment(new Date()).subtract(1,'minute');
                  if (d.isBefore(minuteAgo)) {
                      std = d.format('HH:mm:ss');
                  }else{
                      std = 'just now';
                  }
                }
              }
            }
          }
        }
        return std;
      }
  }

}



