import { Injectable } from '@angular/core';
import { Http, Headers } from '@angular/http';

import 'rxjs/add/operator/toPromise';

import { User } from '../classes/user';

@Injectable()
export class LoginService {
  /**
   * Properties and objects of the service
   */
  public connectionData: any;

  public user: User;

  public initiating: boolean = false;

  private headers;
  /**
   * constructor - Constructor of the service
   */
  constructor(private http: Http) {}
  /**
   * getUserData - Method to call to the API to make login
   *
   * @return {Promise<User>} Returns the response promise
   */
  getUserData(): Promise<User> {
      this.headers = new Headers();
      //this.headers.append('ZendeskAuth',email);
      this.headers.append('ZendeskAuth',this.connectionData.OauthToken);
      this.headers.append('Content-Type', 'application/json');

      return this.http.get(
        this.connectionData.zendeskUrl + 'users/me.json',
        {headers: this.headers})
        .toPromise()
        .then(response => response.json().user as User);
  }
  /**
   * getUserData - Method to call to the API to make login
   *
   * @param {string}  email String with the email to make te login
   * @param {string}  password String with the password to make te login
   *
   * @return {Promise<string>} Returns the response promise
   */
  getOauthToken(email:string,password:string):Promise<string>{
    this.headers = new Headers();
    this.headers.append('Content-Type', 'application/json');
    return this.http
        .post(this.connectionData.zendeskUrl + 'oauth/tokens',
        JSON.stringify({
          username: email,
          password: password
        }),
        {headers: this.headers})
        .toPromise()
        .then(res => res.json().access_token);
  }
}



