import { Injectable } from '@angular/core';
import { Http, Headers } from '@angular/http';

import 'rxjs/add/operator/toPromise';

@Injectable()
export class PayaraService {
  /**
   * Properties and objects of the service
   */
  public connectionData: any;

  public email: string;

  private headers;
  /**
   * constructor - Constructor of the service
   */
  constructor(private http: Http) { }
  /**
   * getStoredEmail - Method to call to the API to get the Zendesk Support Stored Email
   *
   * @return {Promise<string>} Returns the response promise
   */
  getStoredEmail(): Promise<string> {
    if(this.email !== undefined && this.email !== null && this.email !== ''){
      return Promise.resolve(this.email);
    }else{
      this.headers = new Headers();
      this.headers.append('Content-Type', 'application/json');

      return this.http.get(
        this.connectionData.payaraURL + 'configs/config/server-config/zendesk-support-configuration/get-zendesk-support-configuration.json',
        {headers: this.headers})
        .toPromise()
        .then(response => response.json().extraProperties.zendeskSupportConfiguration.emailAddress as string);
    }
  }
  /**
   * setStoredEmail - Method to call to the API to set the Zendesk Support Stored Email
   *
   * @param {string}  email String with the email to set inside domain.xml
   *
   * @return {Promise<string>} Returns the response promise
   */
  setStoredEmail(email:string): Promise<any> {
    this.headers = new Headers();
    this.headers.append('accept', 'application/json');
    this.headers.append('X-Requested-By', 'payara');
    this.headers.append('Content-Type', 'application/json');

    return this.http.post(
      this.connectionData.payaraURL + 'configs/config/server-config/zendesk-support-configuration/set-zendesk-support-configuration',
      JSON.stringify({emailAddress: email}),
      {headers: this.headers})
      .toPromise()
      .then(response => response.json());
  }
  /**
   * postFile - Method to call to the API to get Payara server files by post
   *
   * @param {string}  url String with the url to call
   * 
   * @return {Promise<string>} Returns the response promise
   */
  postFile(url:string): Promise<any> {
    this.headers = new Headers();
    this.headers.append('accept', 'application/json');
    this.headers.append('X-Requested-By', 'payara');
    this.headers.append('Content-Type', 'application/json');

    return this.http.post(
      this.connectionData.payaraURL + url,
      JSON.stringify({
      	"first": "",
      	"target": "server-config",
      	"__remove_empty_entries__": "true"
      }),
      {headers: this.headers})
      .toPromise()
      .then(response => response.json());
  }
  /**
   * getFile - Method to call to the API to get Payara server files
   *
   * @param {string}  url String with the url to call
   *
   * @return {Promise<any>} Returns the response promise
   */
  getFile(url:string): Promise<any> {
      this.headers = new Headers();
      this.headers.append('Content-Type', 'application/json');

      return this.http.get(
        this.connectionData.payaraURL + url,
        this.headers)
        .toPromise()
        .then(response => response);
  }
  /**
   * getServerInstances - Method to call to the API to get Payara server instances
   *
   * @return {Promise<any>} Returns the response promise
   */
  getServerInstances(): Promise<any> {
    this.headers = new Headers();
    this.headers.append('Content-Type', 'application/json');
    return this.http.get(
      this.connectionData.payaraURL + 'servers/server.json',
      this.headers)
      .toPromise()
      .then(response => response.json().extraProperties.childResources);
  }
  /**
   * getInstanceStatus - Method to call to the API to get Payara server instance status
   *
   * @param {string}  id String with the id of the instance
   *
   * @return {Promise<any>} Returns the response promise
   */
  getInstanceStatus(id:string): Promise<any> {
    this.headers = new Headers();
    this.headers.append('Content-Type', 'application/json');
    return this.http.get(
      this.connectionData.payaraURL + 'list-instances.json?id='+id,
      this.headers)
      .toPromise()
      .then(response => response.json().extraProperties.instanceList[0].status);
  }
}



