/**
 * Generic imports
 */
import { BrowserModule } from '@angular/platform-browser';
import { NgModule, enableProdMode } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpModule,Http } from '@angular/http';
import { RouterModule, Routes } from '@angular/router';
import { DatePipe } from '@angular/common';
import { TranslateModule, TranslateLoader } from '@ngx-translate/core';
import { TranslateHttpLoader } from '@ngx-translate/http-loader';

import { ModalModule } from "ngx-modal";

import {ToolTipModule} from 'angular2-tooltip';
import { JWBootstrapSwitchModule } from 'jw-bootstrap-switch-ng2';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';

import { environment } from '../environments/environment';
/**
 * Component imports
 */
import { AppComponent } from './app.component';
import { routing }        from './app.routing';

import { LoginComponent } from './components/login/login.component';
import { ListTicketsComponent } from './components/list-tickets/list-tickets.component';
import { NewTicketComponent } from './components/new-ticket/new-ticket.component';
import { DetailedTicketComponent } from './components/detailed-ticket/detailed-ticket.component';
import { AddFileComponent } from './components/add-file/add-file.component';
import { TicketDataComponent } from './components/ticket-data/ticket-data.component';
import { CommentDataComponent } from './components/comment-data/comment-data.component';

import { LoginService } from './services/login.service';
import { AuthGuard } from './guards/auth.guard';
/**
 * Pipe imports
 */
import { DayTimePipe } from './pipes/day-time.pipe';

export function createTranslateLoader(http: Http) {
    return new TranslateHttpLoader(http, './assets/i18n/', '.json');
}

if(environment.production){
  enableProdMode();
}

@NgModule({
  declarations: [
    AppComponent,
    LoginComponent,
    ListTicketsComponent,
    NewTicketComponent,
    DetailedTicketComponent,
    AddFileComponent,
    TicketDataComponent,
    CommentDataComponent,
    DayTimePipe
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    JWBootstrapSwitchModule,
    FormsModule,
    ReactiveFormsModule,
    HttpModule,
    ModalModule,
    ToolTipModule,
    routing,
    TranslateModule.forRoot({
        loader: {
            provide: TranslateLoader,
            useFactory: (createTranslateLoader),
            deps: [Http]
        }
    })
  ],
  exports: [
    RouterModule,
    DayTimePipe,
    TranslateModule
  ],
  providers: [
    DatePipe,
    DayTimePipe,
    AuthGuard,
    LoginService
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



